<template>
  <div class="hello">
    <h1>排故时间设置</h1>
    <el-row :gutter="20">
      <el-col :span="12">
        <el-date-picker
          v-model="dateSelect"
          type="daterange"
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期"
          value-format="yyyy-MM-dd">
        </el-date-picker>
      </el-col>
      <el-col :span="6">
        <el-button type="primary" @click="onSearch()">查询</el-button>
      </el-col>
    </el-row>
    <el-table
        :data="tableData"
        style="width: 100%"
        stripe
        :default-sort = "{prop: 'date', order: 'ascending'}"
        >
        <el-table-column
          prop="date"
          label="日期"
          sortable
          width="180"
          >
        </el-table-column>
        <el-table-column
          prop="fuzhouTime"
          label="福州排故工时"        
          width="180" >
        </el-table-column>
        <el-table-column
          prop="xiamenTime"
          label="厦门排故工时">
        </el-table-column>
        <el-table-column label="操作" width="80">
        <template slot-scope="scope">
          <el-button
            size="mini" type="primary"
            @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
        </template>
      </el-table-column>



    </el-table>

    <!-- 分页组件 -->


  <el-row>
    <el-col :span="12" :offset="10">
      <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"      
      :page-sizes="[5, 10, 50, 200]"
      :page-size="pageSize"
      :current-page="page"
      layout="total, sizes, prev, pager, next, jumper"
      :total="count">
    </el-pagination>

    </el-col>
  </el-row>


  <el-drawer
  title="编辑"
  :visible.sync="drawer"
  ref="drawer"

  size='30%'>
  <el-form style="margin:0px 45px">
    <el-form-item label="日期" prop="date">
      <el-date-picker type="date" placeholder="日期" v-model="timeForm.date" style="width: 100%;" disabled></el-date-picker>
    </el-form-item>
    <el-form-item label="福州排故工时" prop="fuzhouTime">
      <el-input-number v-model="timeForm.fuzhouTime" :precision="1" :step="0.5" :min="0" :max="300"></el-input-number>
    </el-form-item>
    <el-form-item label="厦门排故工时" prop="xiamenTime">
      <el-input-number v-model="timeForm.xiamenTime" :precision="1" :step="0.5" :min="0" :max="300"></el-input-number>
    </el-form-item>    
    <el-form-item>
              <el-button type="primary" @click="onSubmit('timeForm')">保存信息</el-button>
              <el-button @click="cancel">取消</el-button>
    </el-form-item>

  </el-form>

</el-drawer>



  </div>
</template>

<script>
export default {
  name: 'setTroubleShootingTime',
  data () {
    return {
      dateSelect:[],
      tableTroubleShootingTime:[
        
      ],
      allTimeData:[],
      params:{
        startDate:'2021-10-22',
        endDate:'2021-11-23'
      },
      tableData:[],
      drawer: false,
      timeForm:{
        id:'',
        date:'',
        fuzhouTime:'',
        xiamenTime:'',

      },

      //分页
      count:400,
        page:1,
        pageSize:5,
      
      
    }
  },

  methods: {
    onSearch(){      
      this.params.startDate=this.dateSelect[0]
      this.params.endDate=this.dateSelect[1]
      this.showData()

    },


    handleEdit(index, row) {
        this.drawer = true
        console.log(index, row);
        this.timeForm=row;
      },   

    // dateFormatter(row, column, cellValue, index){        
    //     let dt=new Date(row.date)        
    //     return  dt.getFullYear()+'-'+(dt.getMonth()+1)+'-'+dt.getDate()
    // },

    showData(){
      this.$http.get("http://127.0.0.1:5000/findTroubleShootingTime",{
        params:this.params
      }).then(res=>
            {
              if(res.data.success){
                this.$message({
                message: res.data.msg,
                type: 'success'                
                }); 
                this.allTimeData=res.data.results
                this.count=this.allTimeData.length
                this.tableData=this.allTimeData.slice((this.page-1)*this.pageSize,(this.page*this.pageSize)) //分页显示

              }else{
                this.$message.error(res.data.msg)
              }

            });

    },


    handleCurrentChange(page){ //分页
       this.page=page;
       this.showData();

      },

    handleSizeChange(size){  //分页
        this.pageSize=size;
        this.showData();

      },


    onSubmit(timeForm){
        this.$http.post("http://127.0.0.1:5000/editTroubleShootingTime",this.timeForm).then(res=>{
          console.log(res.data);
          if(res.data.success){
            this.$message({
            message: '恭喜你，'+res.data.msg,
            type: 'success'
            });
            //清空表单
            this.timeForm={};
            //隐藏表单
            this.drawer=false;
            this.showData();
          }else{this.$message.error(res.data.msg)}
      })},

    cancel(){
        this.drawer=false;
        this.showData();


      },





  },

  created(){
      this.showData();

    },





}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



