<template>
  <div class="hello">
    <h3 style="margin:0px 0px">基地排故工时设置</h3>
    <el-divider></el-divider>
    <el-table
      :data="tableData"
      :default-sort = "{prop: 'id', order: 'ascending'}"
      style="width: 100%" stripe border>
      <el-table-column label="id" width="60" prop="id"  sortable></el-table-column>
      <el-table-column label="执行地点" :formatter="baseFormatter"></el-table-column>
      <el-table-column label="执行部门" :formatter="depFormatter"></el-table-column>
      <el-table-column label="能力工时（黄线）"  prop="capWorkHoursY"></el-table-column>
      <el-table-column label="机械专业（黄线）"  prop="MEWorkHoursY"></el-table-column>
      <el-table-column label="电子专业（黄线）"  prop="AVWorkHoursY"></el-table-column>
      <el-table-column label="能力工时（红线）"  prop="capWorkHoursR"></el-table-column>
      <el-table-column label="机械专业（红线）"  prop="MEWorkHoursR"></el-table-column>
      <el-table-column label="电子专业（红线）"  prop="AVWorkHoursR"></el-table-column>

      <el-table-column label="操作" width="80">
        <template slot-scope="scope">
          <el-button
            size="mini" type="primary"
            @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
        </template>
      </el-table-column>
    </el-table>


    <el-drawer
  title="编辑"
  :visible.sync="drawer"
  ref="drawer"

  size='30%'>
  <el-form style="margin:0px 45px">
    <el-form-item label="能力工时（黄线）" prop="capWorkHoursY">
      <el-input-number v-model="timeForm.capWorkHoursY" :precision="1" :step="0.5" :min="0" :max="300"></el-input-number>
    </el-form-item>
    <el-form-item label="机械专业（黄线）" prop="MEWorkHoursY">
      <el-input-number v-model="timeForm.MEWorkHoursY" :precision="1" :step="0.5" :min="0" :max="300"></el-input-number>
    </el-form-item>
    <el-form-item label="电子专业（黄线）" prop="AVWorkHoursY">
      <el-input-number v-model="timeForm.AVWorkHoursY" :precision="1" :step="0.5" :min="0" :max="300"></el-input-number>
    </el-form-item>
    <el-form-item label="能力工时（红线）" prop="capWorkHoursR">
      <el-input-number v-model="timeForm.capWorkHoursR" :precision="1" :step="0.5" :min="0" :max="300"></el-input-number>
    </el-form-item>
    <el-form-item label="机械专业（红线）" prop="MEWorkHoursR">
      <el-input-number v-model="timeForm.MEWorkHoursR" :precision="1" :step="0.5" :min="0" :max="300"></el-input-number>
    </el-form-item>
    <el-form-item label="电子专业（红线）" prop="AVWorkHoursR">
      <el-input-number v-model="timeForm.AVWorkHoursR" :precision="1" :step="0.5" :min="0" :max="300"></el-input-number>
    </el-form-item>
    <el-form-item>
              <el-button type="primary" @click="onSubmit('timeForm')">保存信息</el-button>
              <el-button @click="cancel">取消</el-button>
    </el-form-item>

  </el-form>

</el-drawer>

  </div>
</template>

<script>


export default {
  name: 'setTimeLimit',
  data () {
    return {
      tableData: [],
      depData:[],
      baseData:[],
      drawer: false,
      timeForm:{
        id:'',
        baseId:'',
        depId:'',
        capWorkHoursY:'',
        MEWorkHoursY:'',
        AVWorkHoursY:'',
        capWorkHoursR:'',
        MEWorkHoursR:'',
        AVWorkHoursR:'',

      },
      
    }
  },
  methods: {
      handleEdit(index, row) {
        this.drawer = true
        console.log(index, row);
        this.timeForm=row;
      },   

      depFormatter(row, column, cellValue, index){      //查询depId对应dep
        for(let item of this.depData){
          if(item.id==row.depId){
            return item.dep
          }
        } 
        return '无所属部门'     

      },


      baseFormatter(row, column, cellValue, index){      //查询depId对应dep
        for(let item of this.baseData){
          if(item.id==row.baseId){
            return item.base
          }
        } 
        return '无所属基地'     

      },



      showData(){
        this.$http.get("http://127.0.0.1:5000/findTimeLimit").then(res=>{        
        this.tableData=res.data.results;
        console.log(typeof res.data);
        this.depData=res.data.deps;
        this.baseData=res.data.bases;
        
      })
      }, 

      onSubmit(timeForm){
        this.$http.post("http://127.0.0.1:5000/editTimeLimit",this.timeForm).then(res=>{
          console.log(res.data);
          if(res.data.success){
            this.$message({
            message: '恭喜你，'+res.data.msg,
            type: 'success'
            });
            //清空表单
            this.timeForm={};
            //隐藏表单
            this.drawer=false;
            this.showData();
          }else{this.$message.error(res.data.msg)}
      })},

      cancel(){
        this.drawer=false;
        this.showData();


      },



    },

  created(){
    this.showData();

  },



 
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



